'use client'

import { useState } from 'react'
import { useRouter } from 'next/navigation'
import { services, createBooking } from '@/services/api'
import { useAuth } from '@/context/AuthContext'
import dynamic from 'next/dynamic'
import {
  Box,
  Button,
  TextField,
  Typography,
  MenuItem,
  Select,
  InputLabel,
  FormControl
} from '@mui/material'

function BookingPage() {
  const router = useRouter()
  const { user } = useAuth()

  const [serviceId, setServiceId] = useState<number>(services[0].id)
  const [dateTime, setDateTime] = useState('')
  const [error, setError] = useState('')
  const [success, setSuccess] = useState(false)

  const handleBooking = () => {
    if (!user) {
      router.push('/login')
      return
    }

    if (!dateTime) {
      setError('Bitte Datum und Uhrzeit w√§hlen')
      return
    }

    createBooking(user, serviceId, dateTime)
    router.push('/dashboard')
  }

  return (
    <Box display="flex" flexDirection="column" maxWidth={400} margin="auto" gap={3} mt={10}>
      <Typography variant="h5">Termin buchen</Typography>

      <FormControl fullWidth>
        <InputLabel>Dienstleistung</InputLabel>
        <Select
          value={serviceId}
          label="Dienstleistung"
          onChange={(e) => setServiceId(Number(e.target.value))}
        >
          {services.map((service) => (
            <MenuItem key={service.id} value={service.id}>
              {service.name}
            </MenuItem>
          ))}
        </Select>
      </FormControl>

      <TextField
        label="Datum & Uhrzeit"
        type="datetime-local"
        InputLabelProps={{ shrink: true }}
        value={dateTime}
        onChange={(e) => setDateTime(e.target.value)}
      />

      {error && <Typography color="error">{error}</Typography>}
      {success && <Typography color="primary">Termin erfolgreich gebucht!</Typography>}

      <Button variant="contained" onClick={handleBooking}>
        Buchen
      </Button>
    </Box>
  )
}
export default dynamic(() => Promise.resolve(BookingPage), { ssr: false })
